import React, { memo } from 'react';
import { compose } from 'recompose';
import moment from 'moment';
import PropTypes from 'prop-types';
import { get as _get, find as _find } from 'lodash';
import { TableCell, TableRow } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Formatter from '../../../utils/formatter';
import PAYMENT_TYPES from '../../../enums/invoice';
import styles from './styles';

const InvoicesItemRow = memo(({ index, classes, invoice }) => {
  const paymentType = _get(invoice, 'payment_type', '');
  const paymentTypeTranslated = _get(_find(PAYMENT_TYPES, r => r.key === paymentType), 'label');
  const description = _get(invoice, 'description', '');
  const amount = _get(invoice, 'amount', '');
  const boletoLink = _get(invoice, 'boleto_link', '');
  const createAt = moment(_get(invoice, 'created_at')).format('DD/MM/YYYY');
  return (
    <TableRow className={index % 2 ? classes.bgWhite : classes.bgGray}>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{description}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{Formatter.toCurrency(amount)}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={`${classes.cellText}`}>{paymentTypeTranslated}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{boletoLink}</span>
      </TableCell>
      <TableCell className={classes.tableSmallCell}>
        <span className={classes.cellText}>{createAt}</span>
      </TableCell>
    </TableRow>
  );
});

InvoicesItemRow.propTypes = {
  index: PropTypes.number,
  invoice: PropTypes.object,
  classes: PropTypes.object
};

export default compose(
  withStyles(styles, { withTheme: true })
)(InvoicesItemRow);
